.container{display:flex;justify-content:center;align-items:center;background-color:#000;padding:40px;position:relative}.header{background:#000;color:#fff;text-align:center;font-size:30px;padding:20px}.circle{width:220px;height:220px;border-radius:50%;border:2px solid #00bcd4;position:relative;display:flex;justify-content:center;align-items:center}.skill{position:absolute;background:#00bcd4;color:#fff;padding:6px 12px;border-radius:20px;font-size:14px;white-space:nowrap;transition:transform .2s ease}